const productWrapper = document.querySelector("#product-wrapper");

products.products.forEach((product, index) => {
  const slide = document.createElement("div");
  slide.className = "swiper-slide";
  slide.dataset.product = product.category;

  const link = document.createElement("a");
  link.href = "#card";
  link.className = "shop__product";

  const imageBox = document.createElement("div");
  imageBox.className = "shop__image-box";

  const image = document.createElement("img");
  image.src = product.imageUrl;
  image.alt = product.name;
  image.className = "shop__image";
  image.id = index + 1;

  imageBox.appendChild(image);
  link.appendChild(imageBox);

  const productName = document.createElement("h4");
  productName.className = "shop__product-name";
  productName.textContent = product.name;

  const productPrice = document.createElement("p");
  productPrice.className = "shop__price";
  productPrice.textContent = product.price;

  link.appendChild(productName);
  link.appendChild(productPrice);

  slide.appendChild(link);
  productWrapper.appendChild(slide);
});

const body = document.querySelector(".page__body");
const form = document.querySelector(".contact__form");

function scrollChange() {
  const hash = window.location.hash;

  if (
    hash === "#menu" ||
    hash === "#card" ||
    hash === "#menu-card" ||
    hash === "#shipping" ||
    hash === "#payment" ||
    hash === "#confirmation" ||
    hash === "#cart" ||
    hash === "#all"
  ) {
    body.style.overflow = "hidden";
  } else {
    body.style.overflow = "";
  }
}

function addProduct(categoryId, productName, imageUrl, price, index) {
  const category = document.getElementById(categoryId);
  const productsContainer = category.querySelector(".products");

  var productElement = document.createElement("a");
  productElement.href = "#card";
  productElement.className = "shop__product";

  const imageBox = document.createElement("div");
  imageBox.className = "shop__image-box";

  const image = document.createElement("img");
  image.src = imageUrl;
  image.alt = productName;
  image.className = "shop__image";
  image.id = index + 1;

  const productNameElement = document.createElement("h4");
  productNameElement.className = "shop__product-name";
  productNameElement.textContent = productName;

  const priceElement = document.createElement("p");
  priceElement.className = "shop__price";
  priceElement.textContent = price;

  imageBox.appendChild(image);
  productElement.appendChild(imageBox);
  productElement.appendChild(productNameElement);
  productElement.appendChild(priceElement);

  productsContainer.appendChild(productElement);
}

product.product.forEach((product, index) => {
  addProduct(product.category, product.name, product.imageUrl, product.price, index);
});

document.addEventListener("DOMContentLoaded", function () {
  const formShipping = document.querySelector(".shipping__form");

  formShipping.addEventListener("submit", formSend);

  async function formSend(e) {
    e.preventDefault();

    // eslint-disable-next-line no-undef
    const formData = new FormData(formShipping);

    formShipping.classList.add("_sending");

    // eslint-disable-next-line no-undef
    const response = await fetch("sendmail.php", {
      method: "Post",
      body: formData,
    });

    if (response.ok) {
      const result = await response.json();

      // eslint-disable-next-line no-undef
      alert(result.message);
      formShipping.reset();
      formShipping.classList.remove("_sending");
    } else {
      // eslint-disable-next-line no-undef
      alert("Помилка");
      formShipping.classList.remove("_sending");
    }
  }
});

const shippingLink = document.querySelector(".shipping__link");

const paymentDelivery = document.querySelectorAll(".payment__delivery");

const paymentButton = document.querySelector(".payment__button");
const card = document.querySelector(".cardd");
const del = document.querySelector(".del");

paymentDelivery.forEach((element) => {
  element.addEventListener("click", (event) => {
    switch (event.target.id) {
      case "11":
        paymentButton.setAttribute("href", "#shipping");
        del.style.border = "none";
        card.style.border = "2px solid black";

        if (card.style.border === "2px solid black") {
          shippingLink.style.display = "block";
        }
        break;

      case "12":
        paymentButton.setAttribute("href", "#shipping");
        del.style.border = "2px solid black";
        card.style.border = "none";

        if (del.style.border === "2px solid black") {
          shippingLink.style.display = "none";
        }
        break;
      default:
        break;
    }
  });
});

var cart = document.querySelector(".cart__list");

document.getElementById("add").addEventListener("click", function () {
  var newElement = document.createElement("li");
  newElement.classList.add("product-item");
  var img = document.createElement("img");
  img.classList.add("img");
  img.src = cardImage.src;
  newElement.appendChild(img);
  var title = document.createElement("p");
  title.textContent = cardName.textContent;
  title.classList.add("product-item__title");
  newElement.appendChild(title);
  var price = document.createElement("p");
  price.textContent = cardPrice.textContent;
  newElement.appendChild(price);
  var deleteButton = document.createElement("button");
  deleteButton.classList.add("delete-button");
  deleteButton.textContent = "Видалити";
  deleteButton.addEventListener("click", function () {
    cart.removeChild(newElement);
    updateCartItemCount();
    checkCartItems();
  });
  newElement.appendChild(deleteButton);
  cart.appendChild(newElement);

  updateCartItemCount();
  checkCartItems();
});

function updateCartItemCount() {
  var cartItems = document.getElementById("cart").getElementsByTagName("li");
  var cartItemCounts = document.getElementsByClassName("cart-item-count");
  
  for (var i = 0; i < cartItemCounts.length; i++) {
    cartItemCounts[i].textContent = cartItems.length.toString();
  }
}

document.getElementById("resetCartButton").addEventListener("click", function() {
  var cartItemCount = document.getElementById("cartItemCount");
  cartItemCount.textContent = "0";
});


function checkCartItems() {
  var cartList = document.querySelector(".cart__list");
  var cartItems = cartList.children;

  if (cartItems.length === 0) {
    var checkoutButton = document.getElementById("checkoutButton");
    checkoutButton.style.display = "none";
  } else {
    var checkoutButton = document.getElementById("checkoutButton");
    checkoutButton.style.display = "block";
  }
}

if (cart.length === 0) {
  checkoutButton.style.display = "none";
} else {
  checkoutButton.style.display = "block";
}

window.addEventListener("load", checkCartItems);

